
.left-nav {
  height: 100%;
  .page-wrap {
	  min-height: 100%;
	  .navbar {
      position: relative;
      height: 70px;
      background-color: #d8d8d8;
      border-width: 0;
      .navbar-inner {
        .dashboard-icon {
          position: relative;
          display: table;
          float: left;
          z-index: 3;
          width: 60px;
          height: 70px;
          background-color: #393f44;
          > span {
            display: table-cell;
            color: white;
            text-align: center;
            vertical-align: middle;
          }
          &:focus {
            background-color: #303030;
            pointer-events: none;
            outline: 0;  
          }
          &:hover {
            cursor: pointer;
            background-color: #303030;
          }
        }
      }
      .navbar-outer {
        ul {
          padding: 0;
          list-style-type: none;
          .space-dropdown {
            a {
              background-color: #ededed;
            }
          }
        }
        .navbar-right {
          padding: 20px 0;
          li {
            padding: 5px 20px;
            a {
              padding: 0 !important;
              &:hover {
                background-color: initial;
              }
            }
            &.border-right {
              border-right: 1px solid #303030;
            }
            input[type="search"] {
              -webkit-appearance: textfield;
              -webkit-box-sizing: content-box;
              -moz-box-sizing: content-box;
              box-sizing: content-box;
            	display: none;
            	height: 16px;
            	width: 177px;
            	margin-right: 10px;
            	&:focus {
            	  outline: none;
            	  border-width: 1px;
            	}
            }
          }
        }
      }
    }
    .sidebar {
      display: block;
      float: left;
      width: 60px;
      height: auto;
      background-color: #393f44;
      overflow: visible;
      &-menu {
        height: 100vh;
        background-color: initial;
        .list-group {
          margin: 0;
          background-color: initial;
          border: 0;
          &-item {
            background-color: initial;
            border: 0;
            a {
              display: block;
              padding: 10px 0;
              color: #fff;
              text-align: center;
            }
            &:hover {
              background-color: #ddd;
              cursor: pointer;
            }
          }
        }
      }
    }
    .page-inner {
      position: relative;
      width: ~"calc(e('100% - 60px'))";
      height: 100vh;
      background-color: #fff; 
      overflow: hidden;
    }
    .space-dropdown {
      width: 250px;
      .dropdown-toggle {
        padding: 25px 20px;
        color: #303030;
        font-size: 14px;
      }
      &-menu {
        position: absolute;
        width: 100%;
        z-index: 3;
        background-color: #ededed;
        border: 0;
        box-shadow: none;
        &-item {
          background-color: initial;
          border-top: 1px solid;
          border-top-color:#ccc;
          &__hdr {
            width: 100%;
            padding-left: 20px;
            padding-right: 20px;
          }
          a {
            padding: 25px 20px;
            font-size: 14px;
            color: #303030;
            display: block;
            span {
              min-width: 15px;
            }
            p {
              padding-left: 10px;
            }
            &:hover {
              background-color: #f1f1f1;  
              text-decoration: none;
              cursor: pointer;
            }
          }
        }
        &-list {
          width: 100%;
          list-style-type: none;
          padding:0;
          border-top: 1px solid #303030;
          &-item {
            a {
              display: block;
              padding: 13px 20px !important;
            }
            .user-avatar-icon {
              vertical-align: top;
              margin-top: 10px;
            }
          }
        }
      }
      .selectedSpaceDropdown {
        padding: 15px 20px;
        display: flex;
        img {
          margin-top: 10px;
        }
      }
    }
    .menu-1 {
      color: #fff;
      padding: 25px 0;
      display: block;
      text-align: center;
      background-color: #393f44;
    }
    .secondary-menu {
      position: fixed;
      top:0;
      bottom: -10px;
      left: -280px;
      //z-index: 2;
      width: 280px;
      background-color: #4d5258;
      list-style-type: none;
      padding: 0;
      color:#fff;
      line-height: 1.5;
      transition: all ease-out 0.3s;
      color: #fff;
      overflow: scroll;
      opacity: 0;
      li {
        border-top-color: #303030;
        &:first-child {
          border: 0;
        }
        a {
          font-size: 15px;
          text-align: left;
          color: white;
          &:hover {
            text-decoration: none;
            color:#303030;
          }
        }
      }
    }   
    .expanded {
      left: 60px; 
      transition: all ease-in 0.3s;
      opacity: 1;
    }
    .tab-menu {
      left: 60px;
      opacity: 1;
      height: 100%;
      transition: all ease-in 0.3s;
      &-expanded {
        left: 60px;
        opacity: 1;
        transition: all ease-in 0.3s;
      }
    }
    .list-combo {
      padding: 15px;
      .form-control {
        border:none;
        border-radius: 0px;
        height: 36px;
        color: #fff;
        background-color: #333333;
        &:focus {
          box-shadow: 0 0 3px -1px inset #000000;
        }
      }
    }
  }
}

//existing classes
.dib {
  display: inline-block;
}
.margin-0 {
  margin: 0;
}
.user-dropdown__user-icon {
  display: inline-block;
  vertical-align: middle;
  img {
    width: 24px;
    height: 23px;
    margin-right: 5px;
  }
  .icon-user {
    border-radius: 50%;
  }
}
.padding-left-10 {
  padding-left: 10px;  
}
.padding-top-10 {
  padding-top: 10px;
}
.margin-left-10 {
  margin-left: 10px;
}
.margin-top-10 {
  margin-top: 10px;
}
.margin-bottom-10 {
  margin-bottom: 10px;
}

@media all and (min-width: 768px) {
  .hamburger-icon {
    padding: 25px 20px!important;
    color: #303030;
  }
  .inner-menu {
    position: relative;
    top: 0;
    left: 0;
    z-index: 9;
    height: 100%;
    width: 100%;
    transition: left 0.3s;
  }
}
@media all and (max-width: 768px) {
  .left-nav {
    .page-wrap {   
      .expanded {
        left: 0px; 
        opacity: 1;
        transition: all ease-in 0.3s;
      }
      .togglePanel {
        left: -280px; 
        opacity: 0;
        transition: all ease-in 0.3s;
      }
    } 
  }
  .inner-menu , .inner-menu-2 {
    width: 100%;
    position: absolute;
    top: 0;
    left: -280px;
    height: 100%;
    background-color: #4d5258;
    transition: left 0.3s;
  }
  .inner-menu-2 {
    z-index: 99;
  }
  .closeMenu {
    position: absolute;
    top: 15px;
    right: 10px;
  }
}
.border-0 {
  border-top-color: transparent !important;
}
//dropdown animations

.open > .dropdown-menu {
  -webkit-transform: scale(1, 1);
  transform: scale(1, 1);  
  
}
.open > .dropdown-menu li a {
  color: #000;  
}
.dropdown-menu li a{
  color: #fff;
}
.dropdown-menu {
  -webkit-transform-origin: top;
  transform-origin: top;
  -webkit-animation-fill-mode: forwards;  
  animation-fill-mode: forwards; 
  -webkit-transform: scale(1, 0);
  display: block;
  
  transition: all 0.2s ease-out;
  -webkit-transition: all 0.2s ease-out;
}
.dropup .dropdown-menu {
  -webkit-transform-origin: bottom;
  transform-origin: bottom;  
}

.dropup > .dropdown-menu:after {
  border-bottom: 0;
  border-top: 6px solid rgba(39, 45, 51, 0.9);
  top: auto;
  display: inline-block;
  bottom: -6px;
  content: '';
  position: absolute;
  left: 50%;
  border-right: 6px solid transparent;
  border-left: 6px solid transparent;
}
